import { Component } from 'react';
//import car from '../../images/car.svg';

class Bumps extends Component {
	componentDidMount = () => {
		this.bumps = [];
	}

	componentDidUpdate = (prevProps) => {
		let {map, isCurrentPositionReceived, bumps} = this.props;

		if (map !== prevProps.map || isCurrentPositionReceived !== prevProps.isCurrentPositionReceived || bumps !== prevProps.bumps) {
			let temp = bumps;
			temp.slice(prevProps.bumps.length - 1);
			this.renderBumps(temp);
		}
	}

	componentWillUnmount = () => {
	    this.removeBumps();
	}

	removeBumps = () => {
		if (this.bumps && this.bumps.length > 0) {
	    	this.bumps.forEach(bump => {
	    		bump.setMap(null);
	    	}, () => {
	    		this.bumps = [];  
	    	});   
	    }
	}

	renderBumps = bumps => {
		//this.removeBumps();

		let { map, google, mapCenter} = this.props;
		const maps = google.maps;

		let bumpOption = {
			path: "M-4,0a4,4 0 1,0 8,0a4,4 0 1,0 -8,0",
			fillColor: "red",
			strokeColor: "red",
            fillOpacity: 1.0,
            scale: 2,
            anchor: new maps.Point(0, 0)
		}

		bumps.forEach((bump, index) => {
			this.bumps.push(new maps.Marker({ 
				position: {lat: bump.lat, lng: bump.lng}, 
				//icon: bumpOption, 
				map: this.map, 
				//animation: maps.Animation.DROP 
				zIndex: 100
			}));
			//this.bumps[index].setMap(this.map);
			//this.bumps[index].setZIndex(1000);
		});
	}

	render() {
		return null;
	}
}

export default Bumps;